
.monkey-detail-area {
  width: 1024px;
  background: #fff;
  margin: 20px auto;
}
.monkey-detail-area .title {
  font-size: 24px;
  font-font-weight: 300;
  padding: 30px 30px 16px;
}
.monkey-detail-area .title aside {
  font-size: 12px;
  color: #bbb;
}
.monkey-detail-area .content {
  color: #666;
  padding: 10px 0 30px;
}
.monkey-detail-area .content >* {
  padding: 0 25px;
}
.monkey-detail-area .content h1,
.monkey-detail-area .content h2,
.monkey-detail-area .content h3,
.monkey-detail-area .content h4,
.monkey-detail-area .content h5,
.monkey-detail-area .content h6 {
  margin-bottom: 0.5rem;
}
.monkey-detail-area .content h1 {
  background: #f8f8f8;
  padding: 28px 25px;
  font-size: 16px;
  margin-bottom: 20px;
}
.monkey-detail-area .content p {
  line-height: 1.6;
  margin-bottom: 10px;
}


